<template>
  <div class="wrapper">
    <div class="container">
      <div class="title">交易详情</div>
      <div class="tx">
        ID：
        <span>{{id}}</span>
        <span
          v-clipboard:copy="copyUrlkey"
          v-clipboard:success="onCopy"
          v-clipboard:error="onError"
          class="copy-wrapper"
        >
          <img src="../../assets/index_img/copy.svg" alt />
        </span>
      </div>
      <div class="detai-content">
        <div class="item-content">
          <el-tooltip content="交易时间" :visible-arrow="false" placement="top-start" effect="light">
            <div class="title-content">交易时间</div>
          </el-tooltip>
          <div class="title-text">{{human}}</div>
        </div>
        <div class="item-content">
          <div class="title-content">数量</div>
          <div class="title-text">{{amount}}{{unit}}</div>
        </div>
        <div class="item-content">
          <div class="title-content">区块</div>
          <div class="title-text">{{blockId}}</div>
        </div>
        <div class="item-content">
          <div class="title-content">付款地址</div>
          <div class="title-text">{{sender}}</div>
        </div>
        <div class="item-content">
          <div class="title-content">收款地址</div>
          <div class="title-text">
            <div v-for="(v,i) in recipient" :key="i">{{v.recipientId}}</div>
          </div>
        </div>
        <div class="item-content">
          <div class="title-content">手续费</div>
          <div class="title-text">{{fee}}{{unit}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getdealone } from "../../../api";
import moment from "moment/moment";
export default {
  mounted() {
    this.unit = sessionStorage.getItem("unit")
      ? sessionStorage.getItem("unit")
      : "MCN";
    var id = this.$route.query;
    getdealone(id.id)
      .then((res) => {
        console.log(res.data.data);

        this.sender = res.data.data.sender;

        if (res.data.data.type == 6) {
          for (let v of res.data.data.asset.payments) {
            this.amount =
              this.amount * 1 + (v.amount / 100000000).toFixed(2) * 1;
          }
          this.recipient = res.data.data.asset.payments;
        } else {
          this.amount = (res.data.data.amount / 100000000).toFixed(2);
          this.recipient.push({
            recipientId: res.data.data.recipient,
          });
        }

        this.Nonce = res.data.data.nonce;
        this.id = res.data.data.id;
        this.copyUrlkey = res.data.data.id;
        this.confirmations = res.data.data.confirmations;
        this.blockId = res.data.data.blockId;
        this.fee = res.data.data.fee / 100000000;
        this.human = moment(res.data.data.timestamp.human).format(
          "YYYY-MM-DD HH:mm:ss"
        );
      })
      .catch((err) => {
        window.console.log(err);
      });
  },
  data() {
    return {
      sender: "",
      human: "",
      recipient: [],
      amount: "",
      id: 0,
      confirmations: 0,
      blockId: 0,
      fee: 0,
      Nonce: 0,
      unit: "",
      copyUrlkey: "",
    };
  },
  methods: {
    onCopy() {
      this.$message({
        message: "复制成功",
        type: "success",
      });
    },
    onError() {
      this.$message.error("复制失败");
    },
  },
};
</script>

